  html {
      overflow: auto;
  }

  html,
  body {
      height: 100%;
      background-color: #fff;
      scroll-behavior: smooth;
  }
  #root {
      min-width: 1200px;
  }
  .out {
      background-color: #fff;
      margin: 0 auto;
      margin-right: 0;

      &.show-aside {
          margin-left: 460px;
          margin-right: -460px;
      }
  }

  .vid-wrapper {
      width: 100%;
      position: relative;
      height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center;
      /*需要用padding来维持16:9比例,也就是9除以16*/
  }

  .vid-wrapper video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      outline: none;
      border: none;
  }

  .vid-wrapper-out {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, .8);
      z-index: 200;
      box-sizing: border-box;
      z-index: -1000;
      transition-property: opacity;
      transition-duration: 500ms;
  }
  .home {
      height: 920px;
      background-image: url('./images/home-bg.png');
      background-repeat: no-repeat;
      background-size: 50% 100%;
      background-color: #f2f2f3;
      text-align: center;
      box-sizing: border-box;
      padding-top: 7.7897%;

      .home-logo {
          max-width: 462px;
          width: 25.52%;
          margin-bottom: 80px;
      }

      .home-title {
          display: block;
          margin-bottom: 80px;
          margin-left: auto;
          margin-right: auto;
          max-width: 630px;
          width: 32.81%;
          height: auto;
      }

      .home-text {
          margin: 0 auto;
          max-width: 960PX;
          line-height: 32px;
          font-size: 18px;
          font-weight: 400;
      }
  }

  .home,
  .pixure,
  .size-finder,
  .w-wardrobe,
  .section-5,
  .we-pattern,
  .join-us {
      position: relative;
  }

  .pixure {
      height: 1300px;
      background-image: url('./images/section-2.png');
      background-size: 100% 100%;
  }

  .pixure .pixure-list {
      position: absolute;
      list-style: none;
      display: flex;
      left: 50%;
      margin-left: 150px;
      bottom: 160px;
      font-size: 18px;
      .pixure-list-item {
          color: #fff;
          cursor: pointer;
          font-weight: bold;
          &:not(:first-of-type) {
              margin-left: 60px;
          }
      }
  }

  .pixure .pixure-outputs {
      position: absolute;
      bottom: 270px;
      left: 50%;
      margin-left: 110px;
      list-style: none;
      color: #fff
  }
  .pixure .pixure-outputs-item {
      line-height: 32px;
      font-size: 24px;
  }

  .size-finder {
      height: 1300px;
      background-image: url('./images/section-3.png');
      background-size: 100% 100%;
      padding-top: 1160px;
      box-sizing: border-box;
  }

  .size-finder .apply-now {
      font-size: 24px;
      text-align: center;
  }

  .w-wardrobe {
      height: 1260px;
      background-image: url('./images/section-4.png');
      box-sizing: border-box;
      background-size: 100% 100%;
      padding-top: 1090px;
      .w-wardrobe-list {
          text-align: center;
          font-size: 16px;
      }

      .w-wardrobe-list-item {
          display: inline-block;
          margin-right: 40px;
          margin-left: 40px;
          font-size: 24px;
      }
  }

  .we-pattern {
      height: 2770px;
      background-image: url('./images/section-6.png');
      background-size: 100% 100%;
      padding-top: 1750px;
      box-sizing: border-box;
      .intro {
          text-align: center;
          color: #fff;
          font-size: 24px;
          color: #fff;
          .intro-tip {
              text-align: center;
              line-height: 50px;
              font-weight: bold;
              clear: #fff;
              font-weight: bold;
          }
      }

      .intro-join {
          margin-top: 90px;
          text-align: center;
          font-size: 24px;
      }

      .fixed-hezuo {
          position: absolute;
          top: 1200px;
          left: 50%;
          margin-left: 380px;
          width: 260px;
          transform: translate(-50%, -50%);
          img {
              width: 100%
          }
      }

  }
  .join-us {
      height: 1618px;;
      background-size: 100% 100%;
      background-image: url('./images/section-7.png');
      padding-top: 280px;
      box-sizing: border-box;
      .texts {
          padding-bottom: 280px;
          font-weight: #999;
      }
      .texts p {
          text-align: center;
          line-height: 36px;
          font-size: 28px;
          letter-spacing: 6px;
      }
      .copyright {
          margin-bottom: 200px;
          p {
              text-align: center;
              line-height: 36px;
              font-size: 18px;
              color: #666;
          }
      }
      .qrcodes {
          position: absolute;
          width: 100%;
          bottom: 0;
          display: flex;
          justify-content: center;
          padding-bottom: 50px;
      }

      .qrcodes figure {
          margin: 0;
          margin-left: 60px;
          margin-right: 60px;
          text-align: center;
      }

      .qrcodes figure img {
          width: 160px;
          height: 160px;
          margin-bottom: 10px;
      }

      .qrcodes figure figcaption {
          text-align: center;
          font-size: 32px;
      }
  }